<template>
<div :style='{"border":"2px solid #a4d9b7","padding":"20px","margin":"20px auto 0","borderRadius":"8px","flexWrap":"wrap","background":"#fff","width":"1200px","position":"relative"}'>
    <el-form
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="200px"
    >
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 0px","display":"inline-block"}' label="电桩编号" prop="dianzhuangbianhao">
            <el-input v-model="ruleForm.dianzhuangbianhao" 
                placeholder="电桩编号" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 0px","display":"inline-block"}' label="图片" v-if="type!='cross' || (type=='cross' && !ro.tupian)" prop="tupian">
            <file-upload
            tip="点击上传图片"
            action="file/upload"
            :limit="3"
            :multiple="true"
            :fileUrls="ruleForm.tupian?ruleForm.tupian:''"
            @change="tupianUploadChange"
            ></file-upload>
          </el-form-item>
            <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 0px","display":"inline-block"}' class="upload" v-else label="图片" prop="tupian">
                <img v-if="ruleForm.tupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.tupian.split(',')[0]" width="100" height="100">
                <img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.tupian.split(',')" :src="baseUrl+item" width="100" height="100">
            </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 0px","display":"inline-block"}'  label="电桩状态" prop="dianzhuangzhuangtai">
            <el-select v-model="ruleForm.dianzhuangzhuangtai" placeholder="请选择电桩状态"  >
              <el-option
                  v-for="(item,index) in dianzhuangzhuangtaiOptions"
                  :key="index"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 0px","display":"inline-block"}'  label="电桩分类" prop="dianzhuangfenlei">
            <el-select v-model="ruleForm.dianzhuangfenlei" placeholder="请选择电桩分类"  >
              <el-option
                  v-for="(item,index) in dianzhuangfenleiOptions"
                  :key="index"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 0px","display":"inline-block"}' label="输入电压" prop="shurudianya">
            <el-input v-model="ruleForm.shurudianya" 
                placeholder="输入电压" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 0px","display":"inline-block"}' label="输出功率" prop="shuchugonglv">
            <el-input v-model="ruleForm.shuchugonglv" 
                placeholder="输出功率" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 0px","display":"inline-block"}' label="使用方式" prop="shiyongfangshi">
            <el-input v-model="ruleForm.shiyongfangshi" 
                placeholder="使用方式" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 0px","display":"inline-block"}' label="小时单价" prop="xiaoshidanjia">
            <el-input v-model="ruleForm.xiaoshidanjia" 
                placeholder="小时单价" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 0px","display":"inline-block"}' label="位置" prop="weizhi">
            <el-input v-model="ruleForm.weizhi" 
                placeholder="位置" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 0px","display":"inline-block"}' label="注意事项" prop="zhuyishixiang">
            <el-input v-model="ruleForm.zhuyishixiang" 
                placeholder="注意事项" clearable ></el-input>
          </el-form-item>

      <el-form-item :style='{"padding":"0","margin":"8px 0 0 0"}'>
        <el-button :style='{"border":"0","cursor":"pointer","padding":"0","margin":"20px 40px 20px 260px","outline":"none","color":"rgba(255, 255, 255, 1)","borderRadius":"20px","background":"#47b144","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}'  type="primary" @click="onSubmit">提交</el-button>
        <el-button :style='{"border":"2px solid #3db769","cursor":"pointer","padding":"0","margin":"0","outline":"none","color":"#3db769","borderRadius":"20px","background":"rgba(255, 255, 255, 1)","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' @click="back()">返回</el-button>
      </el-form-item>
    </el-form>
</div>
</template>

<script>
  export default {
    data() {
      return {
        id: '',
        baseUrl: '',
        ro:{
            dianzhuangbianhao : false,
            tupian : false,
            dianzhuangzhuangtai : false,
            dianzhuangfenlei : false,
            shurudianya : false,
            shuchugonglv : false,
            shiyongfangshi : false,
            xiaoshidanjia : false,
            weizhi : false,
            zhuyishixiang : false,
        },
        type: '',
        userTableName: localStorage.getItem('UserTableName'),
        ruleForm: {
          dianzhuangbianhao: '',
          tupian: '',
          dianzhuangzhuangtai: '',
          dianzhuangfenlei: '',
          shurudianya: '',
          shuchugonglv: '',
          shiyongfangshi: '',
          xiaoshidanjia: '',
          weizhi: '',
          zhuyishixiang: '',
        },
        dianzhuangzhuangtaiOptions: [],
        dianzhuangfenleiOptions: [],
        rules: {
          dianzhuangbianhao: [
          ],
          tupian: [
          ],
          dianzhuangzhuangtai: [
          ],
          dianzhuangfenlei: [
          ],
          shurudianya: [
          ],
          shuchugonglv: [
          ],
          shiyongfangshi: [
          ],
          xiaoshidanjia: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          weizhi: [
          ],
          zhuyishixiang: [
          ],
        },
      };
    },
    computed: {



    },
    created() {
	  //this.bg();
      let type = this.$route.query.type ? this.$route.query.type : '';
      this.init(type);
      this.baseUrl = this.$config.baseUrl;
    },
    methods: {
      getMakeZero(s) {
          return s < 10 ? '0' + s : s;
      },
      // 下载
      download(file){
        window.open(`${file}`)
      },
      // 初始化
      init(type) {
        this.type = type;
        if(type=='cross'){
          var obj = JSON.parse(localStorage.getItem('crossObj'));
          for (var o in obj){
            if(o=='dianzhuangbianhao'){
              this.ruleForm.dianzhuangbianhao = obj[o];
              this.ro.dianzhuangbianhao = true;
              continue;
            }
            if(o=='tupian'){
              this.ruleForm.tupian = obj[o].split(",")[0];
              this.ro.tupian = true;
              continue;
            }
            if(o=='dianzhuangzhuangtai'){
              this.ruleForm.dianzhuangzhuangtai = obj[o];
              this.ro.dianzhuangzhuangtai = true;
              continue;
            }
            if(o=='dianzhuangfenlei'){
              this.ruleForm.dianzhuangfenlei = obj[o];
              this.ro.dianzhuangfenlei = true;
              continue;
            }
            if(o=='shurudianya'){
              this.ruleForm.shurudianya = obj[o];
              this.ro.shurudianya = true;
              continue;
            }
            if(o=='shuchugonglv'){
              this.ruleForm.shuchugonglv = obj[o];
              this.ro.shuchugonglv = true;
              continue;
            }
            if(o=='shiyongfangshi'){
              this.ruleForm.shiyongfangshi = obj[o];
              this.ro.shiyongfangshi = true;
              continue;
            }
            if(o=='xiaoshidanjia'){
              this.ruleForm.xiaoshidanjia = obj[o];
              this.ro.xiaoshidanjia = true;
              continue;
            }
            if(o=='weizhi'){
              this.ruleForm.weizhi = obj[o];
              this.ro.weizhi = true;
              continue;
            }
            if(o=='zhuyishixiang'){
              this.ruleForm.zhuyishixiang = obj[o];
              this.ro.zhuyishixiang = true;
              continue;
            }
          }
        }
        // 获取用户信息
        this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            var json = res.data.data;
          }
        });
        this.dianzhuangzhuangtaiOptions = "已预定,可预定".split(',')
        this.dianzhuangfenleiOptions = "落地式,挂壁式".split(',')
      },

    // 多级联动参数
      // 多级联动参数
      info(id) {
        this.$http.get('chongdianzhuang/detail/${id}', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.ruleForm = res.data.data;
          }
        });
      },
      // 提交
      onSubmit() {

        //更新跨表属性
        var crossuserid;
        var crossrefid;
        var crossoptnum;
        this.$refs["ruleForm"].validate(valid => {
          if(valid) {
            if(this.type=='cross'){
                 var statusColumnName = localStorage.getItem('statusColumnName');
                 var statusColumnValue = localStorage.getItem('statusColumnValue');
                 if(statusColumnName && statusColumnName!='') {
                     var obj = JSON.parse(localStorage.getItem('crossObj'));
                     if(!statusColumnName.startsWith("[")) {
                         for (var o in obj){
                             if(o==statusColumnName){
                                 obj[o] = statusColumnValue;
                             }
                         }
                         var table = localStorage.getItem('crossTable');
                         this.$http.post(table+'/update', obj).then(res => {});
                     } else {
                            crossuserid=Number(localStorage.getItem('userid'));
                            crossrefid=obj['id'];
                            crossoptnum=localStorage.getItem('statusColumnName');
                            crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                     }
                 }
            }
            if(crossrefid && crossuserid) {
                 this.ruleForm.crossuserid=crossuserid;
                 this.ruleForm.crossrefid=crossrefid;
                 var params = {
                     page: 1,
                     limit: 10,
                     crossuserid:crossuserid,
                     crossrefid:crossrefid,
                 }
                 this.$http.get('chongdianzhuang/list', {
                  params: params
                 }).then(res => {
                     if(res.data.data.total>=crossoptnum) {
                         this.$message({
                          message: localStorage.getItem('tips'),
                          type: 'success',
                          duration: 1500,
                         });
                          return false;
                     } else {
                         // 跨表计算


                          this.$http.post('chongdianzhuang/add', this.ruleForm).then(res => {
                              if (res.data.code == 0) {
                                  this.$message({
                                      message: '操作成功',
                                      type: 'success',
                                      duration: 1500,
                                      onClose: () => {
                                          this.$router.go(-1);
                                      }
                                  });
                              } else {
                                  this.$message({
                                      message: res.data.msg,
                                      type: 'error',
                                      duration: 1500
                                  });
                              }
                          });
                     }
                 });
             } else {


                  this.$http.post('chongdianzhuang/add', this.ruleForm).then(res => {
                     if (res.data.code == 0) {
                          this.$message({
                              message: '操作成功',
                              type: 'success',
                              duration: 1500,
                              onClose: () => {
                                  this.$router.go(-1);
                              }
                          });
                      } else {
                          this.$message({
                              message: res.data.msg,
                              type: 'error',
                              duration: 1500
                          });
                      }
                  });
             }
          }
        });
      },
      // 获取uuid
      getUUID () {
        return new Date().getTime();
      },
      // 返回
      back() {
        this.$router.go(-1);
      },
      tupianUploadChange(fileUrls) {
          this.ruleForm.tupian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");;
      },
    }
  };
</script>

